module.exports = {
    //
    timestampToTime: function(timestamp, type=1) {
        var date = new Date(timestamp * 1000);//10*1000131000
        let Y = date.getFullYear() + '.';
        let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '.';
        let D = date.getDate() + ' ';
        let h = date.getHours() + ':';
        let m = date.getMinutes()
        let s = date.getSeconds();
        if (type == 1) { //
            return Y+M+D+h+m+':'+s;
        } else if (type == 2) { //
            return Y+M+D;
        } else if (type == 3) { //
            return h+m;
        } else if (type == 4) { //
            return M+D;
        } else if (type == 5) { //
            return Y+M+D+h+m;
        }
    }
}